:root{
  --color-dark:#1a1a1a;
  --color-gray:#737373;
  --color-concrete:#bfbfbf;
  --color-pearl:#fff;
  --color-primary:#0066ff;
  --color-success:rgb(0, 255, 179);
  --color-primary-dark:#001d4d;
  --color-secondary:#ff9933;
  --color-danger:#ff1a1a;
  --radius: 0.5rem;
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
