  .btn-banner{
    width:200px;
    height:60px;
    background: linear-gradient(rgba(252, 252, 252, 0.5),rgba(40,44,52,0.1));
    border:4px solid;
    color:var(--deepcolor);
    font-weight: 700;
    text-transform: uppercase;
    cursor:pointer;
    font-size: 16px;
    position:relative;
    border-radius: 30px;
}
.btn-banner::before, .btn-banner::after{
content: "";
position:absolute;
width:14px;
height:4px; 
background:rgba(253, 253, 253, 0.8);
-webkit-transform:skewX(50deg);
        transform:skewX(50deg);
transition:.4s linear;
}
.btn-banner::before{
    top: -4px;
    left:10%;
}
.btn-banner::after{
    bottom: -4px;
    right:10%;
}
.btn-banner:hover::before{
    left:80%;
}
.btn-banner:hover::after{
    right:80%;
}
.btn-banner:hover{
    color:var(--color-gray);
}
