}
.sidenav ul{
 position:absolute;
  top:0;
  left:0;
  width:100%;
  display:flex;
  flex-direction: column;
  align-items: center;
}
.sidenav ul li{
  list-style:none;
  width:70px;
  z-index: 1;
}
.sidenav ul li a{
  position: relative;
  display:flex;
  justify-content: center;
  align-items: center;
  width:100%;
  text-align:center;
  color:var(--color-pearl);
  font-weight:bold;
}
.sidenav ul li.active a.icon{
  color:var(--color-dark);
  
}
.sidenav ul li a .icon{
  position:relative;
  display:block;
  line-height: 75px;
  text-align: center;
}
.sidenav ul li a .title{
  position:absolute;
  top:50%;
  left:110px;
  color:var(--color-pearl);
  background: var(--color-gray);
  -webkit-transform:translateY(-50%);
          transform:translateY(-50%);
  padding:5px 10px;
  border-radius: 6px;
  transition:0.5s;
  opacity:0;
  visibility: hidden;
  box-shadow:0 5px 15px rgba(0,0,0,0.1);
}
.sidenav ul li:hover a .title{
  opacity: 1;
  visibility:visible;
  -webkit-transform: translateX(-25px) translateY(-50%);
          transform: translateX(-25px) translateY(-50%);
}
.sidenav ul li:hover a .title::before{
  opacity: 1;
 
  visibility:visible;
  -webkit-transform: translateX(-25px) translateY(-50%);
          transform: translateX(-25px) translateY(-50%);
}

.sidenav ul .indicator{
  position:absolute;
  left:0;
  top:0;
  margin:10px;
  width:50px;
  height:50px;
  transition:0.5s;
  /*circle indicating page*/
  background-color:var(--color-gray);
  border:2px solid var(--color-pearl);
  border-radius: 50%;
}
.sidenav ul .indicator::before{
  content:'';
  position:absolute;
  -webkit-transform:translate(-50%,-50%);
          transform:translate(-50%,-50%);
  width:50%;
  height:50%;
  border-radius: 50%;
  transition:0.5s;
}
.sidenav ul li:nth-child(1).active ~ .indicator{
  -webkit-transform: translateY(calc(75px*0));
          transform: translateY(calc(75px*0))
}
.sidenav ul li:nth-child(2).active ~ .indicator{
  -webkit-transform: translateY(calc(75px*1));
          transform: translateY(calc(75px*1))
}
.sidenav ul li:nth-child(3).active ~ .indicator{
  -webkit-transform: translateY(calc(75px*2));
          transform: translateY(calc(75px*2))
}
.sidenav ul li:nth-child(4).active ~ .indicator{
  -webkit-transform: translateY(calc(75px*3));
          transform: translateY(calc(75px*3))
}
