.videoCard {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    grid-column-gap: 1rem;
    grid-row-gap:1rem;
  }
  .videoContainer{
    display:flex;
    flex-direction: column;
    margin: 0 auto;
    max-width: 80%;
    text-align: center;
   
   }
   i{
    font-size: 48px;
   }